.video-component {
  background: $black;
  padding: 40px;

  @include grid-media($lg) {
    height: 530px;
  }


  .heading {
    @include grid-column(12, $no-gutter);

    @include grid-media($lg) {
      @include grid-column(3, $no-gutter);

      height: 450px;
    }


    position: relative;

    h1 {
      color: $white;
      width: 100%;
      top: 0;
      margin: 0;
      font-size: 25px;
      margin-bottom: 25px;

      @include grid-media($lg) {
        position: absolute;
        margin-bottom: 0;
      }
    }
  }

  .video {
    @include grid-column(12, $no-gutter);

    @include grid-media($lg) {
      @include grid-column(6, $no-gutter);

      height: 450px;
    }


    position: relative;

    iframe {
      width: 100%;
      height: 100%;
      margin-bottom: 25px;

      @include grid-media($lg) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 20px;
        margin-bottom: 0;
      }
    }
  }

  .quote {
    @include grid-column(12, $no-gutter);

    @include grid-media($lg) {
      @include grid-column(3, $no-gutter);

      height: 450px;
    }


    position: relative;

    p {
      color: $white;
      font-style: italic;
      width: 100%;
      bottom: 0;
      margin: 0;

      @include grid-media($lg) {
        position: absolute;
      }


      &:after {
        content: "";
        display: block;

        @include grid-column(10, $no-gutter);

        height: 2px;
        background: white;
        margin-top: 20px;
      }
    }
  }
}